import { Component, ElementRef, OnInit, Renderer2, signal, ViewChild } from '@angular/core';
import { toast } from 'ngx-sonner';

@Component({
  selector: 'app-options-value',
  templateUrl: './options-value.component.html',
  styleUrl: './options-value.component.scss',
  standalone: false
})
export class OptionsValueComponent implements OnInit {
  @ViewChild('cellInput', { static: false }) cellInput: ElementRef | undefined;

  rows: number = 11; // Number of rows in the matrix
  cols: number = 7; // Number of columns in the matrix
  matrix: {
    id: string;
    value: number;
    type: number;
    isEditing: boolean;
    isSelected: boolean;
    name: string;
    status: string;
    errorName: string;
    hasError: boolean;
    allowAutoNameEdit: boolean;
  }[][] = []; // Ma trn lu gi tr, kiu, trng thi chnh sa, trng thi chn, tn
  currentType: number = 1; // Kiu hin ti ang chn
  usedNames: Set<number> = new Set(); // Danh sch lu tr cc gi tr  c s dng
  selectedMatrix: {
    id: string;
    value: number;
    type: number;
    name: string;
    status: string;
  }[][] = []; // Ma trn th hai lu gi tr, kiu, trng thi chnh sa, trng thi chn, tn v trng thi ca cc  c chn
  originalName: string = '';
  holdTimeout: any;
  types = [
    {
      value: 1,
      name: 'Gh',
      allowAutoNameEdit: true,
      icon: 'seat-available.svg',
      blockIcon: 'seat-block.svg',
      selectedIcon: 'seat-select',
    },
    { value: 2, name: 'Hnh lang', allowAutoNameEdit: false, icon: 'street.svg' },
    { value: 3, name: 'Ti x', allowAutoNameEdit: false, icon: 'driver.svg' },
  ];

  constructor(private el: ElementRef, private renderer: Renderer2) {
    this.initializeMatrix(); // To ma trn 5x5

    ///temp second matrix
    this.selectedMatrix = Array.from({ length: this.rows }, (_, i) =>
      Array.from({ length: this.cols }, (_, j) => ({
        id: '',
        value: i * this.cols + j + 1,
        type: 0,
        name: '',
        icon: '',
        status: '',
      })),
    );
  }

  // Khi to ma trn v trng thi chn
  // Khi to ma trn v trng thi chn
  initializeMatrix(): void {
    this.matrix = Array.from({ length: this.rows }, (_, i) =>
      Array.from({ length: this.cols }, (_, j) => ({
        id: '',
        value: i * this.cols + j + 1,
        type: 0,
        isEditing: false,
        isSelected: false,
        name: '',
        icon: '',
        errorName: '',
        status: 'available',
        hasError: false,
        allowAutoNameEdit: false, // Thm thuc tnh allowAutoNameEdit
        isDisappearing: false, // Thm thuc tnh isDisappearing
      })),
    );
  }

  // Chn kiu (type)
  selectType(type: number): void {
    this.currentType = type;
  }

  // p dng kiu vo  c chn, khng cho php b chn khi ang chnh sa
  applyType(row: number, col: number): void {
    const cell = this.matrix[row][col];
    const selectedType = this.types.find((type) => type.value === this.currentType);
    // Lu trng thi chnh sa hin ti trc khi p dng loi mi
    this.matrix.forEach((matrixRow, i) => matrixRow.forEach((cell, j) => cell.isEditing && this.saveEdit(i, j)));
    // Kim tra nu  ang chnh sa hoc c li th khng lm g
    if (cell.isEditing || this.hasError()) return;

    // Nu   c chn v loi hin ti ging vi loi ca , b chn 
    if (cell.isSelected && cell.type === this.currentType) {
      cell.isSelected = false;
      this.usedNames.delete(parseInt(cell.name.slice(1))); // Xa tn khi usedNames
      cell.type = 0;
      cell.name = '';
      cell.allowAutoNameEdit = false; // Cp nht allowAutoNameEdit
      return;
    }

    // Nu   c chn v loi hin ti khc vi loi ca , p dng loi mi
    if (cell.isSelected && cell.type !== this.currentType) {
      this.updateCellType(cell, selectedType);
      return;
    }

    // Nu  ang l loi 1 v loi mi l 2 hoc 3, xa tn khi usedNames
    if (cell.type === 1 && [2, 3].includes(this.currentType)) {
      this.usedNames.delete(parseInt(cell.name.slice(1)));
    }

    // Lu trng thi chnh sa hin ti trc khi p dng loi mi
    this.matrix.forEach((matrixRow, i) => matrixRow.forEach((cell, j) => cell.isEditing && this.saveEdit(i, j)));

    // Nu loi hin ti hp l, p dng loi mi cho 
    if (this.currentType > 0) {
      this.updateCellType(cell, selectedType);
    }
  }

  updateCellType(cell: any, selectedType: any): void {
    cell.type = this.currentType; // Cp nht loi ca 
    cell.isSelected = true; // nh du   c chn

    // Cp nht allowAutoNameEdit theo loi
    cell.allowAutoNameEdit = selectedType?.allowAutoNameEdit || false;

    // Nu loi l 2 hoc 3, xa tn ca 
    if ([2, 3].includes(this.currentType)) {
      cell.name = '';
    } else if (selectedType?.allowAutoNameEdit) {
      const maxNames = this.rows * this.cols;
      for (let i = 1; i <= maxNames; i++) {
        if (!this.usedNames.has(i)) {
          cell.name = `A${i.toString().padStart(2, '0')}`; // To tn mi cho 
          this.usedNames.add(i);
          break;
        }
      }
    }

    cell.icon = this.getIconByType(cell.type, cell.status); // Cp nht icon cho 
  }

  // Hm focus vo  ang chnh sa
  focusCell(): void {
    console.log(' ~ OptionsValueComponent ~ focusCell ~ focusCell:');
    setTimeout(() => {
      if (this.cellInput) {
        this.cellInput.nativeElement.focus();
      }
    }, 0);
  }

  // Bt u nhn chut
  onMouseDown(row: number, col: number, event: MouseEvent): void {
    if (event.button !== 0) return; // Ch thc hin nu nhn chut tri
    event.preventDefault(); // Ngn chn hnh ng mc nh
    this.holdTimeout = setTimeout(() => {
      this.toggleStatus(row, col, event); // Thay i trng thi khi nhn gi
      this.holdTimeout = null; // t li holdTimeout
    }, 1000); // Thi gian nhn gi l 1000ms
  }

  // Nh chut
  onMouseUp(row: number, col: number, event: MouseEvent): void {
    if (event.button !== 0) return; // Ch thc hin nu nh chut tri
    if (this.holdTimeout) {
      clearTimeout(this.holdTimeout); // Hy b m thi gian nu nhn gi cha xy ra
      this.applyType(row, col); // Thc hin hnh ng nhn
    }
    this.holdTimeout = null; // t li holdTimeout
  }

  // Di chut ra khi 
  onMouseLeave(event: MouseEvent): void {
    if (this.holdTimeout) {
      clearTimeout(this.holdTimeout); // Hy b m thi gian nu nhn gi cha xy ra
      this.holdTimeout = null; // t li holdTimeout
    }
  }

  // Nhn chut
  onClick(row: number, col: number, event: MouseEvent): void {
    if (event.button !== 0) return; // Ch thc hin nu nhn chut tri
    if (this.holdTimeout) {
      clearTimeout(this.holdTimeout); // Hy b m thi gian nu nhn gi cha xy ra
      this.holdTimeout = null; // t li holdTimeout
      this.applyType(row, col); // Thc hin hnh ng nhn
    }
  }

  getIconByType(type: number, status: string = 'available'): string {
    const selectedType = this.types.find((t) => t.value === type);
    if (type === 1 && status === 'block' && selectedType?.blockIcon) {
      return `assets/icons/${selectedType.blockIcon}`; // ng dn n icon c bit
    }
    return selectedType?.icon ? `assets/icons/${selectedType.icon}` : '';
  }

  // Kim tra xem  c ang  ch  chnh sa
  isEditing(row: number, col: number): boolean {
    return this.matrix[row][col].isEditing;
  }

  // Bt u chnh sa  khi nhn chut phi
  startEdit(row: number, col: number, event: MouseEvent): void {
    event.preventDefault(); // Ngn chn menu chut phi mc nh
    const cell = this.matrix[row][col];
    if (cell.type > 0 && cell.type !== 2 && cell.type !== 3) {
      this.originalName = cell.name; // Lu gi tr name hin ti
      cell.isEditing = true; // Bt u ch  chnh sa
      this.focusCell();
    }
  }

  saveEdit(row: number, col: number): void {
    const cell = this.matrix[row][col];
    const newName = cell.name;

    // Kim tra nh dng tn (phi bt u bng 'A' v theo sau l s t 01 n 99)
    const nameFormat = /^A\d{2}$/;
    if (!nameFormat.test(newName)) {
      toast.error('Tn khng hp l. Tn phi c nh dng A01, A02, ..., A99.');
      cell.hasError = true; // nh du  c li
      cell.isEditing = true; // Gi   ch  chnh sa
      this.focusCell(); // Focus vo  li
      return;
    }

    // Kim tra nu tn mi  c s dng v khc vi tn hin ti
    if (newName !== this.originalName && this.usedNames.has(parseInt(newName.slice(1)))) {
      toast.error('Tn ny  c s dng. Vui lng chn tn khc.');
      cell.hasError = true; // nh du  c li
      cell.isEditing = true; // Gi   ch  chnh sa
      this.focusCell(); // Focus vo  li
      return;
    }

    // Xa tn c khi danh sch  s dng
    this.usedNames.delete(parseInt(this.originalName.slice(1)));

    // Thm tn mi vo danh sch  s dng
    this.usedNames.add(parseInt(newName.slice(1)));

    cell.hasError = false; // Xa nh du li
    cell.isEditing = false; // Kt thc ch  chnh sa
  }

  saveSelected(): void {
    // nh x d liu t cc  c chn trong ma trn 1 sang ma trn 2
    const selectedCells = this.matrix.flat().filter((cell) => cell.isSelected);

    selectedCells.forEach((cell) => {
      const row = Math.floor((cell.value - 1) / this.cols);
      const col = (cell.value - 1) % this.cols;

      // Kim tra v gn GUID nu cha c
      if (!cell.id) {
        cell.id = "1";
      }

      this.selectedMatrix[row][col] = {
        id: cell.id,
        value: cell.value,
        type: cell.type,
        name: cell.name,
        status: cell.status,
      };
    });

    this.updateDisplayMatrix();
    console.log('Selected Cells:', selectedCells);
    toast.success('D liu  c lu thnh cng!');
  }


  resetSelected() {
    this.initializeMatrix();
    this.usedNames.clear();
  }

  // Phng thc thay i trng thi ca  trong ma trn th hai
  toggleStatus(row: number, col: number, event: MouseEvent): void {
    event.preventDefault(); // Ngn chn menu chut phi mc nh
    const cell = this.matrix[row][col];
    if (cell.type === 2 || cell.type === 3) {
      return; // Khng cho php click nu type l 2 hoc 3
    }

    // Remove the current status class
    const cellElement = this.el.nativeElement.querySelector(`#cell-${row}-${col}`);
    this.renderer.removeClass(cellElement, `status-${cell.status}`);

    setTimeout(() => {
      if (cell.status === 'available') {
        cell.status = 'block';
      } else if (cell.status === 'block') {
        cell.status = 'available';
      }

      // Add the new status class
      this.renderer.addClass(cellElement, `status-${cell.status}`);
    }, 300); // Ensure the revert animation completes before applying the new status
  }

  hasError(): boolean {
    return this.matrix.some((row) => row.some((cell) => cell.hasError));
  }

  displayRows: boolean[] = [];
  visibleColumns: boolean[] = [];
  selectedColumns: number[] = [];

  ngOnInit() {
    this.updateDisplayMatrix();
  }

  // Phng thc cp nht displayRows v visibleColumns
  updateDisplayMatrix() {
    const rows = this.selectedMatrix.length;
    const cols = this.selectedMatrix[0].length;

    // Khi to mng trng thi hin th
    this.displayRows = Array(rows).fill(false);
    this.visibleColumns = Array(cols).fill(false);
    this.selectedColumns = [];
    const selectedRows = new Set<number>(); // Tp hp cc hng c chn

    // Cp nht trng thi hin th da trn cc  c chn
    this.selectedMatrix.forEach((row, i) => {
      row.forEach((cell, j) => {
        if (cell.type > 0) {
          this.displayRows[i] = true;
          this.selectedColumns.push(j);
          selectedRows.add(i); // Thm hng c chn vo tp hp
        }
      });
    });

    // m bo rng tt c cc hng gia hng u tin v hng cui cng u c hin th
    const selectedRowsArray = Array.from(selectedRows).sort((a, b) => a - b);
    const firstSelectedRow = selectedRowsArray[0];
    const lastSelectedRow = selectedRowsArray[selectedRowsArray.length - 1];

    for (let i = firstSelectedRow; i <= lastSelectedRow; i++) {
      this.displayRows[i] = true;
    }

    // Sp xp cc ct c chn
    this.selectedColumns.sort((a, b) => a - b);

    // Hin th cc ct t ct u tin c chn n ct cui cng c chn
    if (this.selectedColumns.length > 0) {
      const firstCol = this.selectedColumns[0];
      const lastCol = this.selectedColumns[this.selectedColumns.length - 1];
      for (let j = firstCol; j <= lastCol; j++) {
        this.visibleColumns[j] = true;
      }
    }
  }
}
